<template>
	<view class="content">
		<form>
			<view class="cu-form-group">
				<view class="title">购买清单</view>
			</view>
			<view class="cu-form-group">
				<image class="avator" :src="dianyingInfo.dianyingPhoto" mode=""></image>
				<view class="title">
					<view>{{dianyingInfo.dianyingName}}</view>
					<view >
						{{selected}}号
						<text style="margin-left: 30upx;color: red;">￥{{dianyingInfo.dianyingNewMoney}}</text>
					</view>
				</view>
			</view>
			<view class="cu-form-group">
				<view class="title">总价</view>
				<view>
					<text>￥{{maxNewMnuey}}</text>
				</view>
			</view>
		</form>
		<view class="padding" style="text-align: center;">
			<button  @tap="onSubmitTap()" class="bg-red lg">确认支付</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				user: {},
				orderGoods: {},
				dianyingInfo: {},
				selected: "",
				maxNewMnuey: 0,
			}
		},
		async onLoad(options) {
			// 获取订单信息，座位信息
			this.orderGoods = uni.getStorageSync('orderGoods');
			let aaa = await this.$api.info("dianying",this.orderGoods.dianyingId)
			this.dianyingInfo = aaa.data
			this.selected = this.orderGoods.selected
			this.maxNewMnuey = (this.orderGoods.selected.split(",")).length * this.dianyingInfo.dianyingNewMoney
		},
		async onShow() {
			let table = uni.getStorageSync("nowTable");
			let res = await this.$api.session(table)
			this.user = res.data
			
		},
		methods: {
			async onSubmitTap() {
				let _this = this;
				let order = {
					dianyingOrderUuidNumber:_this.$utils.genTradeNo(),
					yonghuId: this.user.id,
					dianyingId: this.dianyingInfo.id,
					buyZuowei: this.selected
				}
				 _this.$utils.msgBack('支付成功');
				 await _this.$api.add('dianyingOrder', order);
			},
		}
	}
</script>

<style lang="scss">
	.avator {
		width: 150upx;
		height: 150upx;
		margin: 20upx 0;
	}
</style>
